<template>
  <div class="container">
    <div class="row">
      <div class="col-auto mx-auto">
        <ul id="nav">
          <RouterLink
            
            :to="{ name: 'mainpage' }"
          >
         home
          </RouterLink>
          <a class="" @click="logout">log out</a>
        </ul>
      </div>
    </div>
    <form @click.prevent>
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input
            type="text"
            placeholder="Enter the resturant name"
            v-model="name"
          />
          <span v-if="!nameValid && name.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="tel" placeholder="Enter the number" v-model="phone" />
          <span v-if="!phoneValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="text" placeholder="Enter the adress" v-model="adress" />
          <span v-if="!adressValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <div
            class="alert alert-success"
            v-if="goodMessage.length > 0"
            role="alert"
          >
            {{ goodMessage }}
          </div>
          <div
            class="alert alert-danger"
            role="alert"
            v-if="errorMessage.length > 0"
          >
            {{ errorMessage }}
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-auto mx-auto g-3">
          <button type="button" class="btn btn-primary" @click="addnewRes">
            Add now
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import axios from 'axios';
import { mapActions } from 'vuex';
export default {
  data() {
    return {
      userID: '',
      name: '',
      phone: '',
      adress: '',
      nameValid: true,
      phoneValid: true,
      adressValid: true,
      goodMessage: '',
      errorMessage: '',
    };
  },
  methods: {
    ...mapActions(['redirectto']),
    logout() {
      localStorage.clear();
      this.$router.push({ name: 'login' });
    },
    async addnewRes() {
      if (this.name.length <= 0) {
        this.nameValid = false;
      } else if (this.phone.length <= 0) {
        this.phoneValid = false;
      } else if (this.adress.length <= 0) {
        this.adressValid = false;
      } else {
        let results = await axios.post('http://localhost:3000/location', {
          name: this.name,
          phone: this.phone,
          adress: this.adress,
          userid: this.userID,
        });

        if (results.status === 201) {
          localStorage.setItem('local-item', JSON.stringify(results.data));
          this.goodMessage = 'it has been added';
          this.errorMessage = '';
          setTimeout(() => {
            (this.name = ''), (this.adress = ''), (this.phone = '');
            this.goodMessage = '';
            this.nameValid = true;
            this.phoneValid = true;
            this.adressValid = true;
          }, 2000);
          this.redirectto({ val: 'mainpage' });
        } else {
          this.errorMessage = "it doesn't work, please try again";
        }
      }
    },
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    this.userID = JSON.parse(user).id;
    if (!user) {
      this.redirectto({ val: 'signup' });
    }
    let result = await axios.get(
      `http://localhost:3000/location?userid=${this.userid}`
    );
    if (result.status === 200 && result.data.length > 0) {

      this.listLoc = result.data;
    }
  },
};
</script>

<style scoped>
span {
  color: red;
}
input {
  display: block;
}
ul a {
  text-decoration: none;
  color: darkcyan;
  margin-right: 19px;
  font-weight: bold;
  cursor: pointer;
}
</style>
