<template>
  <div class="container">
    <MainPage></MainPage>
    <form @click.prevent>
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <h1 class="">Update profile</h1>
          <input type="text" placeholder="Enter the name" v-model="name" />
          <span v-if="!nameValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="email" placeholder="Enter the email" v-model="email" />
          <span v-if="!emailValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="password" placeholder="Enter the pass" v-model="pass" />
          <span v-if="!passValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto mx-auto">
          <button class="btn btn-info" @click="updateProfile" type="submit">
            update profile now
          </button>
          &nbsp;&nbsp;&nbsp; &nbsp;
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import MainPage from './MainPage.vue';
import { mapActions } from 'vuex';
import axios from 'axios';
import userValidate from '@vuelidate/core';
import { required, email, minLength } from '@vuelidate/validators';
export default {
  components: {
    MainPage,
  },
  data() {
    return {
      v$: userValidate(),
      name: '',
      pass: '',
      email: '',
      userID: '',
      nameValid: true,
      passValid: true,
      emailValid: true,
    };
  },
  validations() {
    return {
      name: { required, minLength: minLength(10) },
      email: { required, email },
      pass: { required, minLength: minLength(10) },
    };
  },
  methods: {
    ...mapActions(['redirectto']),
    async updateProfile() {
      if (this.name.length <= 0) {
        this.nameValid = false;
      } else if (this.pass.length <= 0) {
        this.passValid = false;
      } else if (this.email.length <= 0) {
        this.emailValid = false;
      } else {
        let results = await axios.put(
          `http://localhost:3000/users/${this.userID}`,
          {
            name: this.name,
            pass: this.pass,
            email: this.email,
          }
        );

        if (results.status === 200) {
          localStorage.setItem('user-item', JSON.stringify(results.data));
          this.redirectto({ val: 'mainpage' });
        } else {
          console.log('bad');
        }
      }
    },
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (user) {
      this.name = JSON.parse(user).name;
      this.email = JSON.parse(user).email;
      this.pass = JSON.parse(user).pass;
      this.userID = JSON.parse(user).id;
    } else {
      this.redirectto({ val: 'Signup' });
    }
  },
};
</script>

<style scoped>
input {
  display: block;
}
span {
  color: red;
}
</style>
