<template>
  <div class="container">
    <NavBar></NavBar>
    <RouterLink :to="{ name: 'menu', params: { locationId: locationId } }">
      <button class="btn btn-info">back to menu</button>
    </RouterLink>
    <div class="text-center">
      <h1>{{ locName }}</h1>
      <p class="text-muted">{{ locAdress }}</p>
    </div>

    <form @click.prevent>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto mb-3">Add new item</div>
      </div>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto">
          <div class="form-floating mb-3">
            <input
              type="text"
              v-model="name"
              class="form-control w250"
              id="itemName"
              placeholder="enter the item name"
            />
            <label for="itemName">Enter Item name </label>
          </div>
          <span v-if="!nameValid && name.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto">
          <div class="form-floating mb-3">
            <input
              type="number"
              v-model="price"
              class="form-control w250"
              id="itemPrice"
              placeholder="enter the item price"
            />
            <label for="itemPrice">Enter Item price </label>
          </div>
          <span v-if="!priceValid && price.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto">
          <div class="form-floating mb-3">
            <input
              type="number"
              v-model="qty"
              class="form-control w250"
              id="itemqty"
              placeholder="Enter Item Quantities"
            />
            <label for="itemqty">Enter Item Quantities </label>
          </div>
          <span v-if="!qtyValid && qty.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto">
          <div class="form-floating mb-3">
            <textarea
              class="form-control w250 h200"
              placeholder="Leave a Description here"
              v-model="desc"
              id="floatingTextarea"
            ></textarea>
            <label for="floatingTextarea">Description</label>
          </div>
          <span v-if="!descValid && desc.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <div class="row g3 align-items-center">
        <div class="col-auto d-block mx-auto">
          <div class="form-floating">
            <select
              v-model="pickedCat"
              class="form-select w250"
              id="floatingSelect"
              aria-label="Floating label select example"
            >
              <option
                v-for="(cat, i) in listOFcaterogies"
                :key="i"
                :value="cat.id"
              >
                {{ cat.name }}
              </option>
            </select>
            <label for="floatingSelect">select the category</label>
          </div>
          <span v-if="!pickValid && pickedCat.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <div class="row">
        <div class="col-auto mx-auto g-3">
          <button
            type="button"
            class="w250 btn btn-warning mb-4"
            @click="updateItem"
          >
            Upadate Now
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import { mapGetters } from 'vuex';
//   import { RouterLink } from 'vue-router';
import axios from 'axios';
export default {
  components: {
    NavBar,
  },
  data() {
    return {
      userId: '',
      userName: '',
      locationId: '',
      locName: '',
      locAdress: '',
      name: '',
      qty: 1,
      itemId: this.$route.params.itemId,
      desc: '',
      pickedCat: '',
      price: '',
      priceValid: true,
      nameValid: true,
      qtyValid: true,
      descValid: true,
      pickValid: true,
    };
  },
  computed: {
    ...mapGetters([
      'isLogedin',
      'userLogedID',
      'listOFcaterogies',
      'numOfCaterogies',
    ]),
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;

      this.userName = JSON.parse(user).name;
      this.isLogedIn();
      this.locationId = this.$route.params.locationID;
      this.displayAll({ useridis: this.userId, locationIdIs: this.locationId });

      this.canUserSee({
        useridis: this.userId,
        locationIdIs: this.locationId,

        redirecttoPage: 'mainpage',
      });
      this.canUserSeeItem({
        useridis: this.userId,
        locationIdIs: this.locationId,
        itemIdis: this.itemId,
        redirecttoPage: 'mainpage',
      });
      this.getuser(this.userId, this.locationId);
      this.getItem(this.userId, this.locationId, this.itemId);
    }
  },
  methods: {
    ...mapActions(['redirectto']),
    ...mapMutations([
      'isLogedIn',
      'displayAll',
      'canUserSee',
      'canUserSeeItem',
    ]),
    async getuser(userId, locId) {
      let results = await axios.get(
        `http://localhost:3000/location?userid=${userId}&id=${locId}`
      );
      let details = [];
      if (results.status == 200) {
        details = results.data;
        this.locName = details[0].name;
        this.locAdress = details[0].adress;
      }
    },
    async getItem(userID, locID, itemIID) {
      let res = await axios.get(
        `http://localhost:3000/items?userid=${userID}&locId=${locID}&id=${itemIID}`
      );

      if (res.status == 200) {
        let resultdata = res.data[0];
        this.desc = resultdata.desc;
        this.name = resultdata.name;
        this.price = resultdata.price;
        this.qty = resultdata.qty;
        this.pickedCat = resultdata.catId;
        // this.userId = resultdata.userId;
      }
    },
    async updateItem() {
      if (this.name.length <= 0) {
        this.nameValid = false;
      } else if (this.qty.length <= 0) {
        this.qtyValid = false;
      } else if (this.desc.length <= 0) {
        this.descValid = false;
      } else if (this.pickedCat.length <= 0) {
        this.pickValid = false;
      } else if (this.price.length <= 0) {
        this.priceValid = false;
      } else {
        let results = await axios.put(
          
            `http://localhost:3000/items/${this.itemId}`,
          {
            name: this.name,
            qty: this.qty,
            desc: this.desc,
            userid: this.userId,
            locId: +this.locationId,
            catId: this.pickedCat,
            price: this.price,
          }
        );

        if (results.status === 200) {
          localStorage.setItem('local-item', JSON.stringify(results.data));

          setTimeout(() => {
            (this.name = ''),
              (this.qty = 1),
              (this.desc = ''),
              (this.pickedCat = '');
            this.qtyValid = true;
            this.nameValid = true;
            this.descValid = true;
            this.pickValid = true;
          }, 2000);
          this.redirectto({ val: 'menu' });
        } else {
          console.log('');
        }
      }
    },
  },
};
</script>

<style scoped>
.w250 {
  min-width: 250px;
  margin-top: 10px;
}
.h200 {
  min-height: 200px;
}
span {
  color: red;
}
</style>
