<template>
  <div class="container">
    <ul id="nav">
      <!-- tag="li" -->
      <RouterLink :to="{ name: 'mainpage' }"> home </RouterLink>
      <a class="" @click="logout">log out</a>
    </ul>
    <WelcomesMessage></WelcomesMessage>
    <RouterLink to="/LocationPage">
      <button type="button" class="btn btn-primary">Add new Resturant</button>
    </RouterLink>
    <users-loc :userloc="listLoc"></users-loc>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import WelcomesMessage from './WelcomesMessage.vue';
import UsersLoc from './UsersLoc.vue';
import { RouterLink } from 'vue-router';
import axios from 'axios';
export default {
  components: {
    WelcomesMessage,
    UsersLoc,
    RouterLink,
  },
  data() {
    return {
      userid: '',
      listLoc: [],
    };
  },
  methods: {
    ...mapActions(['redirectto']),
    logout() {
      localStorage.clear();
      this.$router.push({ name: 'login' });
    },
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    this.userid = JSON.parse(user).id;
    console.log(this.userid);
    if (user) {
      this.redirectto({ payload: 'mainpage' });
    } else {
      this.redirectto({ payload: 'Signup' });
    }
    let result = await axios.get(
      `http://localhost:3000/location?userid=${this.userid}`
    );
    if (result.status === 200 && result.data.length > 0) {
      this.listLoc = result.data;
    }
  },
};
</script>

<style scoped>
ul {
  display: flex;
  justify-content: center;
  list-style: none;
  margin-bottom: 50px;
}
ul a {
  text-decoration: none;
  color: darkcyan;
  margin-right: 19px;
  font-weight: bold;
  cursor: pointer;
}
</style>
