<template>
  <div class="conatainer">
    <NavBar></NavBar>
    <div class="row">
      <div class="col-auto mx-auto">
        <form @click.prevent>
          <h1>Delete Resturant</h1>
          <p class="text-danger">
            are you sure you want to delete this resturant
          </p>
          {{ name }} <br />
          {{ adress }} <br />
          {{ phone }} <br />
        </form>
      </div>
    </div>
    <div class="row">
      <div class="col-auto mx-auto">
        <button class="btn btn-info" @click="goback">Go Back</button>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <button class="btn btn-danger" @click="deleteloction">
          Delete Now
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import axios from 'axios';
export default {
  data() {
    return {
      name: '',
      adress: '',
      phone: '',
      userId: '',
      deleteLoc: '',
      locationData: '',
      AllItemsId: [],
      allcatID: [],
      locationID: this.$route.params.locationID,
    };
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;
      this.deleteLoc = this.$route.params.locationID;
      this.canUserAccess();
      let results = await axios.get(
        `http://localhost:3000/items?locId=${this.locationID}`
      );
      let reslen = results.data.length;
      for (let i = 0; i < reslen; i++) {
        this.AllItemsId.push(results.data[i].id);
      }
      let result = await axios.get(
        `http://localhost:3000/categories?locationid=${this.locationID}`
      );
      let rescatlen = result.data.length;
      for (let i = 0; i < rescatlen; i++) {
        this.allcatID.push(result.data[i].id);
      }
      
    }
  },
  components: {
    NavBar,
  },
  methods: {
    ...mapActions(['redirectto']),
    goback() {
      this.redirectto({ val: 'mainpage' });
    },
    async canUserAccess() {
      let result = await axios.get(
        `http://localhost:3000/location?id=${this.deleteLoc}&userid=${this.userId}`
      );
      if (result.status == 200 && result.data.length > 0) {
        this.locationData = result.data;
        this.name = this.locationData[0].name;
        this.phone = this.locationData[0].phone;
        this.adress = this.locationData[0].adress;
      } else {
        this.redirectto({ val: 'home' });
      }
    },
    async deleteloction() {
      let resultt = await axios.delete(
        `http://localhost:3000/location/${this.deleteLoc}`
      );
      let allCatresults = [];
      for (let i = 0; i < this.allcatID.length; i++) {
        let results = await axios.delete(
          `http://localhost:3000/categories/${this.allcatID[i]}`
        );
        if (results.status == 200) {
          allCatresults.push(true);
        } else {
          allCatresults.push(false);
        }
      }
      let allitems = [];
      for (let i = 0; i < this.AllItemsId.length; i++) {
        let results = await axios.delete(
          `http://localhost:3000/items/${this.AllItemsId[i]}`
        );
        if (results.status == 200) {
          allitems.push(true);
        } else {
          allitems.push(false);
        }
      }

      if (
        resultt.status == 200 &&
        !allCatresults.includes(false) &&
        !allitems.includes(false)
      ) {
        this.redirectto({ val: 'mainpage' });
      } else {
        console.log('something went wrong');
      }
    },
  },
};
</script>
