<template>
  <form @click.prevent></form>

  <div class="row">
    <div class="col-auto mx-auto">
      <h1 class="">login</h1>
      <div class="form-floating">
        <input
          type="text"
          v-model="email"
          class="form-control w250"
          id="loginEmail"
          placeholder="enter the item name"
        />
        <label for="loginEmail">Enter the Email </label>
      </div>
      <!-- <input type="email" placeholder="Enter the email" v-model="email" /> -->
      <br />
      <span v-if="emptyEmail">you should write the right email</span>
    </div>
  </div>
  <br />
  <div class="row">
    <div class="col-auto mx-auto">
      <div class="form-floating ">
        <input
          type="text"
          v-model="pass"
          class="form-control w250"
          id="password"
          placeholder="enter the item name"
        />
        <label for="password">Enter the password </label>
      </div>
      <br />
      <span v-if="emptyPass">enter the right pass</span>
    </div>
  </div>
  <br />
  <div class="row">
    <div class="col-auto mx-auto">
      <button class="btn btn-info" @click="login" type="submit">login</button>
      &nbsp;&nbsp;&nbsp;
      <button
        class="btn btn-info"
        @click="redirectto({ val: 'Signup' })"
        type="submit"
      >
        Signup
      </button>
    </div>
  </div>
  <br />
  <div class="row">
    <div class="col-auto mx-auto">
      <span v-if="userNotFound">user not found</span>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';
export default {
  data() {
    return {
      pass: '',
      email: '',
      name: '',
      emptyPass: false,
      emptyEmail: false,
      userNotFound: false,
    };
  },
  computed: {
    emailData() {
      return this.$store.state.email;
    },
    passData() {
      return this.$store.state.pass;
    },
  },
  methods: {
    async login() {
      if (this.pass.length <= 0) {
        this.emptyPass = true;
      }
      if (this.email.length <= 0) {
        this.emptyEmail = true;
      } else {
        let results = await axios.get(
          `http://localhost:3000/users?email=${this.email}&&pass=${this.pass}`
        );

        if (results.status === 200 && results.data.length > 0) {
          localStorage.setItem('user-item', JSON.stringify(results.data[0]));
          this.redirectto({ val: 'mainpage' });
        } else {
          this.userNotFound = true;
        }
      }
    },
    ...mapActions(['redirectto']),
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (user) {
      this.redirectto({ val: 'mainpage' });
    }
  },
};
</script>

<style scoped>
span {
  font-weight: bold;
  color: red;
}
input {
  display: block;
}
.w250 {
  min-width: 250px;
  margin-top: 10px;
}
</style>
