<template>
  <div class="conatainer">
    <NavBar></NavBar>
    <div class="row">
      <div class="col-auto mx-auto">
        <form @click.prevent>
          <h1>Delete Category</h1>
          <p class="text-danger">
            are you sure you want to delete this Category {{ name }} ?
          </p>
        </form>
      </div>
    </div>
    <div class="row">
      <div class="col-auto mx-auto">
        <button class="btn btn-info" @click="goback">Go Back</button>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <button class="btn btn-danger" @click="deleteCat">Delete Now</button>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import axios from 'axios';
export default {
  data() {
    return {
      name: '',
      CatNme: '',
      getName: [],
      locationId: this.$route.params.locationID,
      caterogryId: this.$route.params.catId,
      userId: '',
      deleteLoc: '',
      locationData: '',
      AllItemsId: [] ,
    };
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;
      this.deleteLoc = this.$route.params.locationID;
      this.canUserAccess();
      this.canUserSeeCat({
        useridis: this.userId,
        locationIdIs: this.locationId,
        catIdIs: this.caterogryId,
        redirecttoPage: 'mainpage',
      });
      this.getTheName(this.userId, this.locationId, this.caterogryId);
      let results = await axios.get(
        `http://localhost:3000/items?catid=${this.caterogryId}`
      );
      let reslen = results.data.length;
      for (let i = 0; i < reslen; i++) {
        this.AllItemsId.push(results.data[i].id);
      }
    }
  },
  components: {
    NavBar,
  },
  methods: {
    ...mapMutations(['isLogedIn', 'displayAll', 'canUserSee', 'canUserSeeCat']),
    ...mapActions(['redirectto']),
    async getTheName(userid, locid, catid) {
      let res = await axios.get(
        `http://localhost:3000/categories?userid=${userid}&locationid=${locid}&id=${catid}`
      );

      if (res.status == 200) {
        this.getName = res.data;

        if (this.getName.length > 0) {
          this.name = this.getName[0].name;
        }
      }
    },
    goback() {
      this.redirectto({ val: 'ViewCaterogy' });
    },
    async canUserAccess() {
      let result = await axios.get(
        `http://localhost:3000/location?id=${this.deleteLoc}&userid=${this.userId}`
      );

      if (result.status == 200 && result.data.length > 0) {
        this.locationData = result.data;
        this.name = this.locationData[0].name;
      } else {
        this.redirectto({ val: 'home' });
      }
    },
    async deleteCat() {
      let result = await axios.delete(
        `http://localhost:3000/categories/${this.caterogryId}`
      );
      let allitems = [];
      for (let i = 0; i < this.AllItemsId.length; i++) {
        let results = await axios.delete(
          `http://localhost:3000/items/${this.AllItemsId[i]}`
        );
        if (results.status == 200) {
          allitems.push(true);
        } else {
          allitems.push(false);
        }
      }
      if (result.status == 200 && !allitems.includes(false)) {
        this.redirectto({ val: 'ViewCaterogy' });
      } else {
        console.log('something went wrong');
      }
    },
  },
};
</script>
