<template>
  <div class="container">
    <p class="text-end">
      welcome {{ getTheName }}
      <router-link :to="{ name: 'profile' }">
        <button class="btn btn-info" type="button">Profile</button>
      </router-link>
    </p>
  </div>
</template>

<script>
export default {
  computed: {
    getTheName() {
      // return this.$store.state.name;
      let datastorge = localStorage.getItem('user-item');
     let otherdata = JSON.parse(datastorge).name ;
      let getname = otherdata;
      return getname;
    },
  },
};
</script>

