<template>
  <div class="container">
    <NavBar></NavBar>
    <div class="row">
      <div class="col-auto mx-auto">
        <form @click.prevent>
          <h1>Delete All</h1>
          <p class="text-danger">
            are you sure you want to delete all resturant
          </p>
          <p class="text-danger">
            it will delete everything in the resturant,,, be careful
          </p>
        </form>
      </div>
    </div>
    <div class="row">
      <div class="col-auto mx-auto">
        <button class="btn btn-info" @click="goback">Go Back</button>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <button class="btn btn-danger" @click="deleteAll">Delete All</button>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import axios from 'axios';
export default {
  components: {
    NavBar,
  },
  data() {
    return {
      userId: '',
      LocId: [],
      catId: [],
      itemsId: [],
    };
  },
  methods: {
    ...mapActions(['redirectto']),
    goback() {
      this.redirectto({ payload: 'mainpage' });
    },
    async deleteAll() {
      for (let i = 0; i < this.catId.length; i++) {
        let result = await axios.delete(
          `http://localhost:3000/categories/${this.catId[i]}`
        );
        if (result.status == 200) {
          this.redirectto({ val: 'mainpage' });
        } else {
          console.log('bad');
        }
      }
      for (let i = 0; i < this.itemsId.length; i++) {
        let result = await axios.delete(
          `http://localhost:3000/items/${this.itemsId[i]}`
        );
        if (result.status == 200) {
          this.redirectto({ val: 'mainpage' });
        } else {
          console.log('bad');
        }
      }
      for (let i = 0; i < this.LocId.length; i++) {
        let result = await axios.delete(
          `http://localhost:3000/location/${this.LocId[i]}`
        );
        if (result.status == 200) {
          this.redirectto({ val: 'mainpage' });
        } else {
          console.log('bad');
        }
      }
    },
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;
      let result = await axios.get(
        `http://localhost:3000/location?userid=${this.userId}`
      );
      let resultlength = result.data.length;
      for (let i = 0; i < resultlength; i++) {
        this.LocId.push(result.data[i].id);
      }
      let resultCats = await axios.get(
        `http://localhost:3000/categories?userid=${this.userId}`
      );
      let resultCatslen = resultCats.data.length;
      for (let i = 0; i < resultCatslen; i++) {
        this.catId.push(resultCats.data[i].id);
      }
      let resultItems = await axios.get(
        `http://localhost:3000/items?userid=${this.userId}`
      );
      let resultItemslength = resultItems.data.length;
      for (let i = 0; i < resultItemslength; i++) {
        this.itemsId.push(resultItems.data[i].id);
      }
    }
  },
};
</script>
