<template>
  <div class="container">
    <NavBar></NavBar>
    <RouterLink
      :to="{ name: 'ViewCaterogy', params: { locationID: locationId } }"
    >
      <button class="btn btn-success float-start">View Caterogy</button>
    </RouterLink>
    &nbsp;&nbsp;&nbsp;
    <RouterLink :to="{ name: 'menu', params: { locationID: locationId } }">
      <button class="btn btn-info">back to menu</button>
    </RouterLink>
    <br />
    <div class="text-center">
      <h1>{{ locName }}</h1>
      <p class="text-muted">{{ locAdress }}</p>
    </div>
    <form @click.prevent>
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input
            type="text"
            placeholder="Enter the resturant name"
            v-model="name"
          />
          <span v-if="!nameValid && name.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <br />

      <div class="row">
        <div class="col-auto mx-auto g-3">
          <button type="button" class="btn btn-primary" @click="addnewRes">
            Add now
          </button>
        </div>
      </div>
      <div class="row" v-if="IsNameExist">
        <div class="col-auto mx-auto g-3">
          <p>sorry this name is already existed</p>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import axios from 'axios';

export default {
  components: {
    NavBar,
  },
  data() {
    return {
      locationId: this.$route.params.locationID,
      userId: '',
      locName: '',
      locAdress: '',
      nameValid: true,
      name: '',
      listofCater: [],
      IsNameExist: false,
    };
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;
      this.canUserSee({
        useridis: this.userId,
        locationIdIs: this.locationId,

        redirecttoPage: 'mainpage',
      });
    }
    this.getuser(this.userId, this.locationId);
    this.catergoryInfo(this.userId, this.locationId);
  },
  methods: {
    ...mapActions(['redirectto']),
    ...mapMutations(['isLogedIn', 'displayAll', 'canUserSee']),
    async getuser(userId, locId) {
      let results = await axios.get(
        `http://localhost:3000/location?userid=${userId}&id=${locId}`
      );
      let details = [];
      if (results.status == 200) {
        details = results.data;

        this.locName = details[0].name;
        this.locAdress = details[0].adress;
      }
    },
    async catergoryInfo(userId, locId) {
      let results = await axios.get(
        `http://localhost:3000/categories?userid=${userId}&locationid=${locId}`
      );
      if (results.status == 200) {
        this.listofCater = results.data;
      }
    },
    async addnewRes() {
      let filterCategory = this.listofCater.filter(
        (v) => v.name.toLowerCase() === this.name.toLowerCase()
      );
      if (filterCategory.length > 0) {
        this.IsNameExist = true;
      } else {
        this.IsNameExist = false;
        if (this.name.length <= 0) {
          this.nameValid = false;
        } else {
          let results = await axios.post('http://localhost:3000/categories', {
            name: this.name,
            userid: this.userId,
            locationid: this.locationId,
            // this.redirectto({val : 'ViewCaterogy' })
            
          }  );
          if (results.status == 201) {
            this.redirectto({ val: 'ViewCaterogy' });
          }
        }
      }
    },
  },
};
</script>
