import { createApp } from 'vue';
import { createRouter, createWebHistory } from 'vue-router';
import { createStore } from 'vuex';
import App from './App.vue';
import axios from 'axios';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap';
import '/assest/Global.css';
import 'normalize.css';
import SignUp from './SignLogin/SignUp.vue';
import LoginPage from './SignLogin/LoginPage.vue';
import MainPage from './SignLogin/MainPage.vue';
import ProfilePage from './SignLogin/ProfilePage.vue';
import TryingError from './SignLogin/TryingError.vue';
import LocationPage from './SignLogin/LocationPage.vue';
import DeleteLoc from './SignLogin/DeleteLoc.vue';
import DeleteCat from './SignLogin/DeleteCat.vue';
import DeleteAll from './SignLogin/DeleteAll.vue';
import DeleteAllcat from './SignLogin/DeleteAllcat.vue';
import EditElement from './SignLogin/EditElement.vue';
import MenuPage from './SignLogin/MenuPage.vue';
import ViewCaterogy from './SignLogin/ViewCaterogy.vue';
import NewCaterogy from './SignLogin/NewCaterogy.vue';
import NewItem from './SignLogin/NewItem.vue';
import UpdatePage from './SignLogin/UpdatePage.vue';
import UpdateItem from './SignLogin/UpdateItem.vue';
import DeleteItem from './SignLogin/DeleteItem.vue';
import DeleteitemsAll from './SignLogin/DeleteitemsAll.vue';

let router = createRouter({
  history: createWebHistory(),
  routes: [
    {
      path: '/signup',
      component: SignUp,
      name: 'Signup',
    },
    {
      path: '/LocationPage',
      component: LocationPage,
      name: 'LocationPage',
    },
    {
      path: '/DeleteAll',
      component: DeleteAll,
      name: 'DeleteAll',
    },
    {
      path: '/menu/caterogy/view-caterogy/delete-all/:locationID',
      component: DeleteAllcat,
      name: 'DeleteAllcat',
    },
    {
      path: '/menu/location/:locationID',
      component: MenuPage,
      name: 'menu',
    },
    {
      path: '/menu/add/:locationID',
      component: NewItem,
      name: 'NewItem',
    },
    {
      path: '/menu/item/update/loc/:locationID/:itemId',
      component: UpdateItem,
      name: 'UpdateItem',
    },
    {
      path: '/menu/item/delete/loc/:locationID/item/:itemId',
      component: DeleteItem,
      name: 'DeleteItem',
    },
    {
      path: '/menu/item/delete-all/loc/:locationID',
      component: DeleteitemsAll,
      name: 'DeleteitemsAll',
    },
    {
      path: '/menu/location/:locationID/:catId',
      component: UpdatePage,
      name: 'UpdatePage',
    },
    {
      path: '/menu/location/delete/:locationID/:catId',
      component: DeleteCat,
      name: 'DeleteCat',
    },
    {
      path: '/delete-loc/:locationID',
      component: DeleteLoc,
      name: 'DeleteLoc',
    },
    {
      path: '/edit/:locationID',
      component: EditElement,
      name: 'EditElement',
    },
    {
      path: '',
      redirect: '/Signup',
    },
    {
      path: '/mainpage',
      component: MainPage,
      name: 'mainpage',
    },
    {
      path: '/menu/caterogy/view-caterogy/:locationID',
      component: ViewCaterogy,
      name: 'ViewCaterogy',
    },
    {
      path: '/menu/caterogy/new-caterogy/:locationID',
      component: NewCaterogy,
      name: 'NewCaterogy',
    },
    {
      path: '/login',
      component: LoginPage,
      name: 'login',
    },
    {
      path: '/',
      component: App,
      name: 'home',
    },
    {
      path: '/profile',
      component: ProfilePage,
      name: 'profile',
    },
    {
      path: '/:catchAll(.*)',
      component: TryingError,
      name: 'TryingError',
    },
  ],
});
router.beforeEach((to, _, next) => {
  if (to.params.pagetitle !== undefined) {
    document.title = `${to.name} |  ${process.env.VUE_APP_TITTLE}`;
  } else {
    document.title = `${to.name} | ${process.env.VUE_APP_TITTLE}`;
  }
  next();
});

let store = createStore({
  state() {
    return {
      email: '',
      pass: '',
      name: '',
      isLoggedIn: '',
      userLogedID: '',
      listOFcaterogies: [],
      listOfLocations: [],
      listofItems: [],
      numOfCaterogies: '',
    };
  },
  mutations: {
    redirectto(state , payload) {
      router.push({ name: payload });
    },
   
    isLogedIn(state) {
      let user = localStorage.getItem('user-item');
      if (user) {
        state.isLoggedIn = true;
        state.userLogedID = JSON.parse(user).id;
      } else {
        state.isLoggedIn = false;
      }
    },
    async displayAll(state, payload) {
      let result = await axios.get(
        `http://localhost:3000/categories?userid=${payload.useridis}&locationid=${payload.locationIdIs}`
      );

      if (result.status == 200) {
        state.listOFcaterogies = result.data;
        state.numOfCaterogies = state.listOFcaterogies.length;
      }
    },
    async canUserSee(state, payload) {
      let res = await axios.get(
        `http://localhost:3000/categories?userid=${payload.useridis}&locationid=${payload.locationIdIs}`
      );

      if (res.status == 200) {
        state.listOfLocations = res.data;

        if (state.listOfLocations.length < 0) {
          this.commit('redirectto', payload.redirecttoPage);
        }
      }
    },
    async canUserSeeCat(state, payload) {
      let res = await axios.get(
        `http://localhost:3000/categories?userid=${payload.useridis}&locationid=${payload.locationIdIs}&id=${payload.catIdIs}`
      );

      if (res.status == 200) {
        state.listOfLocations = res.data;

        if (state.listOfLocations.length !== 1) {
          this.commit('redirectto', payload.redirecttoPage);
        }
      }
    },
    async canUserSeeItem(state, payload) {
      let res = await axios.get(
        `http://localhost:3000/items?userid=${payload.useridis}&locId=${payload.locationIdIs}&id=${payload.itemIdis}`
      );

      if (res.status == 200) {
        state.listofItems = res.data;

        if (state.listofItems.length < 1) {
          this.commit('redirectto', payload.redirecttoPage);
        }
      }
    },
  },
  actions: {
    redirectto(context, payload) {
      context.commit('redirectto', payload.val);
    },
  },
  getters: {
    emailStat(state) {
      return state.email;
    },
    passState(state) {
      return state.pass;
    },
    emailState(state) {
      return state.name;
    },
    isLogedin(state) {
      return state.isLoggedIn;
    },
    userLogedID(state) {
      return state.userLogedID;
    },
    listOFcaterogies(state) {
      return state.listOFcaterogies;
    },
    numOfCaterogies(state) {
      return state.numOfCaterogies;
    },
  },
});
let app = createApp(App);
app.use(router);
app.use(store);
app.mount('#app');
