<template>
  <div class="container">
    <NavBar></NavBar>
    <form @click.prevent>
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <h1>update resturant</h1>
        </div>
      </div>
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input
            type="text"
            placeholder="Enter the resturant name"
            v-model="name"
          />
          <span v-if="!nameValid && name.length <= 0"
            >you can't leave this empty</span
          >
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="tel" placeholder="Enter the number" v-model="phone" />
          <span v-if="!phoneValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <input type="text" placeholder="Enter the adress" v-model="adress" />
          <span v-if="!adressValid">you can't leave this empty</span>
        </div>
      </div>
      <br />
      <div class="row">
        <div class="col-auto g-3 mx-auto">
          <div
            class="alert alert-success"
            v-if="goodMessage.length > 0"
            role="alert"
          >
            {{ goodMessage }}
          </div>
          <div
            class="alert alert-danger"
            role="alert"
            v-if="errorMessage.length > 0"
          >
            {{ errorMessage }}
          </div>
        </div>
      </div>
      <div class="row">
        <div class="col-auto mx-auto g-3">
          <button type="button" class="btn btn-success" @click="addnewRes">
            Update now
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import axios from 'axios';
import { mapActions } from 'vuex';
export default {
  components: {
    NavBar,
  },
  data() {
    return {
      name: '',
      phone: '',
      adress: '',
      userID: '',
      locationID: '',
      nameValid: true,
      phoneValid: true,
      adressValid: true,
      goodMessage: '',
      errorMessage: '',
    };
  },
  async mounted() {
    let user = localStorage.getItem('user-item');

    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userID = JSON.parse(user).id;
      this.locationID = this.$route.params.locationID;
   
      this.canUserAccess();
    }
  },
  methods: {
    ...mapActions(['redirectto']),
    logout() {
      localStorage.clear();
      this.$router.push({ name: 'login' });
    },
    async canUserAccess() {
      let result = await axios.get(
        `http://localhost:3000/location?id=${this.locationID}&userid=${this.userID}`
      );

      if (result.status == 200 && result.data.length > 0) {
        this.locationData = result.data;
        this.name = this.locationData[0].name;
        this.phone = this.locationData[0].phone;
        this.adress = this.locationData[0].adress;
      } else {
        this.redirectto({ val: 'home' });
      }
    },
    async addnewRes() {
      if (this.name.length <= 0) {
        this.nameValid = false;
      } else if (this.phone.length <= 0) {
        this.phoneValid = false;
      } else if (this.adress.length <= 0) {
        this.adressValid = false;
      } else {
        let result = await axios.put(
          `http://localhost:3000/location/${this.locationID}`,
          {
            name: this.name,
            phone: this.phone,
            adress: this.adress,
            userid: this.userID,
          }
        );

        if (result.status === 200) {
          localStorage.setItem('local-item', JSON.stringify(result.data));
          this.goodMessage = 'it has been added';
          this.errorMessage = '';
          setTimeout(() => {
            (this.name = ''), (this.adress = ''), (this.phone = '');
            this.goodMessage = '';
            this.nameValid = true;
            this.phoneValid = true;
            this.adressValid = true;
          }, 2000);
          this.redirectto({ val: 'mainpage' });
        } else {
          this.errorMessage = "it doesn't work, please try again";
        }
      }
    },
  },
};
</script>

<style scoped>
input {
  display: block;
}
span {
  color: red;
}
</style>
