<template>
  <form @click.prevent>
    <div class="row">
      <div class="col-auto g-3 mx-auto">
        <h1 class="">sign up here</h1>
        <div class="form-floating">
          <input
            type="text"
            v-model="name"
            class="form-control w250"
            id="loginname"
            placeholder="enter the item name"
          />
          <label for="loginname">Enter the name </label>
        </div>
        <span v-if="!nameValid">you can't leave this empty</span>
      </div>
    </div>
    <br />
    <div class="row">
      <div class="col-auto g-3 mx-auto">
        <div class="form-floating">
          <input
            type="text"
            v-model="email"
            class="form-control w250"
            id="loginEmail"
            placeholder="enter the item name"
          />
          <label for="loginEmail">Enter the Email </label>
        </div>
        <span v-if="!emailValid">you can't leave this empty</span>
        <p>{{ messageExist }}</p>
      </div>
    </div>
    <br />
    <div class="row">
      <div class="col-auto g-3 mx-auto">
        <div class="form-floating">
          <input
            type="text"
            v-model="pass"
            class="form-control w250"
            id="loginpass"
            placeholder="enter the item name"
          />
          <label for="loginpass">Enter the password </label>
        </div>
        <span v-if="!passValid">you can't leave this empty</span>
      </div>
    </div>
    <br />
    <div class="row">
      <div class="col-auto mx-auto">
        <button class="btn btn-info w250" @click="valditSignup" type="submit">
          sign up now
        </button>
      </div>
    </div>
    <div class="row">
      <div class="col-auto mx-auto">
        <button class="btn btn-success w250" @click="login" type="submit">
          you have an account ? login
        </button>
      </div>
    </div>
  </form>
</template>

<script>
import { mapActions } from 'vuex';
import axios from 'axios';
import useVuelidate from '@vuelidate/core';
import { required, email, minLength } from '@vuelidate/validators';
export default {
  data() {
    return {
      v$: useVuelidate(),
      name: '',
      pass: '',
      email: '',
      nameValid: true,
      passValid: true,
      emailValid: true,
      userEmailExist: '',
      messageExist: '',
    };
  },
  Validations() {
    return {
      pass: { required, minLength: minLength(3) },
      name: { required, minLength: minLength(3) },
      email: { required, email, minLength: minLength(3) },
    };
  },

  mounted() {
    console.log('sign up we are here');
    let user = localStorage.getItem('user-item');
    if (user) {
      this.redirectto({ val: 'mainpage' });
    }
  },
  methods: {
    async valditSignup() {
      let res = await axios.get(
        `http://localhost:3000/users?email=${this.email}`
      );
      if (res.status == 200) {
        this.userEmailExist = res.data;
        console.log(this.userEmailExist.length);
        if (this.userEmailExist.length == 1) {
          this.messageExist = ' this email is already exist';
        } else {
          this.signup();
        }
      }
    },
    async signup() {
      if (this.name.length <= 0) {
        this.nameValid = false;
      } else if (this.pass.length <= 0) {
        this.passValid = false;
      } else if (this.email.length <= 0) {
        this.emailValid = false;
      } else {
        let results = await axios.post('http://localhost:3000/users', {
          name: this.name,
          pass: this.pass,
          email: this.email,
        });
        // let datastorge = localStorage.getItem('user-item');
        // this.$store.state.name = JSON.parse(datastorge).name ;
        if (results.status === 201) {
          localStorage.setItem('user-item', JSON.stringify(results.data));

          this.redirectto({ val: 'mainpage' });
        }
      }
    },
    login() {
      this.$router.push({ name: 'login' });
    },
    ...mapActions(['redirectto']),
  },
};
</script>

<style scoped>
span {
  color: red;
  font-weight: bold;
}
input {
  display: block;
}
.w250 {
  min-width: 250px;
  margin-top: 10px;
}
</style>
