<template>
  <div class="container">
    <NavBar></NavBar>
    <div>
      <RouterLink :to="{ name: 'ViewCaterogy', params: { id: locationId } }">
        <button class="btn btn-success float-start">View caterogy</button>
      </RouterLink>

      <RouterLink :to="{ name: 'NewItem', params: { locId: locationId } }">
        <button class="btn btn-success float-end">Add new item</button>
      </RouterLink>
      <div class="clearfix"></div>
      <RouterLink
        v-if="listOFcaterogies.length > 0"
        :to="{
          name: 'DeleteitemsAll',
          params: { locId : locationId },
        }"
      >
        <!-- <div class="clearfix"></div> -->
        <button class="btn btn-danger mt-5">Delete All</button>
      </RouterLink>
    </div>
    <div class="clearfix"></div>
    <div class="text-center">
      <h1>{{ locName }}</h1>
      <p class="text-muted">{{ locAdress }}</p>
    </div>
    <div class="each-cat">
      <div class="" v-for="(cat, i) in listOfCat" :key="i">
        <div class="row">
          <div class="row col-12 catName">
            <h3 class="text-center bg-light p-1">{{ cat.name }}</h3>
          </div>
        </div>

        <div
          v-for="(items, h) in listOfItems"
          :key="h"
          v-show="cat.id == items.catId"
          :class="{ 'col-xs-12 col-sm-4 px-4': cat.id == items.catId }"
        >
          <div
            v-if="cat.id == items.catId"
            :class="{ 'each-item': cat.id == items.catId }"
          >
            <h4 class="item-name">{{ items.name }}</h4>
            <p class="text-muted item-desc">
              {{ items.desc }}
            </p>
            <div>
              <div class="avilable-item float-start text-muted">
                Avalibal qty {{ numberWithCommas(items.qty) }}
              </div>
              <div class="float-end text-muted item-price">
                price : {{ numberWithCommas(items.price) }} $
              </div>
              <div class="clearfix"></div>
            </div>
            <div class="mt-4">
              <div class="avilable-item float-start text-muted">
                <RouterLink
                  :to="{
                    name: 'UpdateItem',
                    params: { locationID: locationId, itemId: items.id },
                  }"
                >
                  <button class="btn btn-info">Update</button>
                </RouterLink>
              </div>
              <div class="float-end text-muted item-price">
                <RouterLink
                  :to="{
                    name: 'DeleteItem',
                    params: { locationID: locationId, itemId: items.id },
                  }"
                >
                  <button class="btn btn-danger">Delete</button>
                </RouterLink>
              </div>
              <div class="clearfix"></div>
            </div>
            <br />
          </div>
        </div>
        <hr />
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import { mapGetters } from 'vuex';
import { RouterLink } from 'vue-router';
import axios from 'axios';
export default {
  components: {
    NavBar,
    RouterLink,
  },
  data() {
    return {
      userId: '',
      userName: '',
      locationId: '',
      locName: '',
      locAdress: '',
      listOfCat: [],
      listOfItems: [],
    };
  },
  computed: {
    ...mapGetters([
      'isLogedin',
      'userLogedID',
      'listOFcaterogies',
      'numOfCaterogies',
    ]),
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;

      this.userName = JSON.parse(user).name;
      this.isLogedIn();
      this.locationId = this.$route.params.locationID;
      this.displayAll({ useridis: this.userId, locationIdIs: this.locationId });
      console.log(this.locationId);
      this.canUserSee({
        useridis: this.userId,
        locationIdIs: this.locationId,

        redirecttoPage: 'mainpage',
      });
      this.getuser(this.userId, this.locationId);
      this.getCat(this.userId, this.locationId);
      this.getItems(this.userId, this.locationId);
    }
  },
  methods: {
    ...mapActions(['redirectto']),
    ...mapMutations(['isLogedIn', 'displayAll', 'canUserSee']),
    numberWithCommas(x) {
      return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    },
    async getuser(userId, locId) {
      let results = await axios.get(
        `http://localhost:3000/location?userid=${userId}&id=${locId}`
      );
      let details = [];
      if (results.status == 200) {
        details = results.data;
        this.locName = details[0].name;
        this.locAdress = details[0].adress;
      }
    },
    async getCat(userID, LocID) {
      let res = await axios.get(
        `http://localhost:3000/categories?userid=${userID}&locationid=${LocID}`
      );

      if (res.status == 200) {
        this.listOfCat = res.data;
      }
    },
    async getItems(userID, LocID) {
      let res = await axios.get(
        `http://localhost:3000/items?userid=${userID}&locId=${LocID}`
      );

      if (res.status == 200) {
        this.listOfItems = res.data;
      }
    },
  },
};
</script>

<style scoped>
.item-desc {
  padding-top: 12px;
  min-height: 100px;
}
</style>
