<template>
  <div class="conatainer">
    <NavBar></NavBar>
    <div class="row">
      <div class="col-auto mx-auto">
        <div class="text-center">
          <h1>{{ locName }}</h1>
          <p class="text-muted">{{ locAdress }}</p>
        </div>
        <form @click.prevent>
          <h1 class="text-center">Delete Item # {{ itemId }}</h1>
          <h3 class="text-center">Delete {{ name }}</h3>
          <p class="text-danger">are you sure you want to delete this item ?</p>
        </form>
      </div>
    </div>
    <div class="row">
      <div class="col-auto mx-auto">
        <RouterLink :to="{ name: 'menu', params: { locationId: locationId } }">
      <button class="btn btn-info">back to menu</button>
    </RouterLink>
        &nbsp;&nbsp;&nbsp;&nbsp;
        <button class="btn btn-danger" @click="deleteItem">Delete Now</button>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import axios from 'axios';
export default {
  data() {
    return {
      name: '',
      CatNme: '',
      getName: [],
      locationId: this.$route.params.locationID,
      itemId: this.$route.params.itemId,
      userId: '',
      deleteLoc: '',
      locationData: '',
      AllItemsId: [],
      locName: '',
      locAdress: '',
      Itemname: '',
      qty: 1,
      desc: '',
      pickedCat: '',
      price: '',
    };
  },
  async mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;

      this.canUserAccess();
      this.canUserAccessLocation();
      this.canUserSeeItem({
        useridis: this.userId,
        locationIdIs: this.locationId,
        itemIdis: this.itemId,
        redirecttoPage: 'mainpage',
      });
      this.getTheName(this.userId, this.locationId, this.itemId);
      let results = await axios.get(
        `http://localhost:3000/items?id=${this.itemId}`
      );
      let reslen = results.data.length;
      for (let i = 0; i < reslen; i++) {
        this.AllItemsId.push(results.data[i].id);
      }
    }
  },
  components: {
    NavBar,
  },
  methods: {
    ...mapMutations([
      'isLogedIn',
      'displayAll',
      'canUserSee',
      'canUserSeeItem',
    ]),
    ...mapActions(['redirectto']),
    async getTheName(userid, locid, itemId) {
      let res = await axios.get(
        `http://localhost:3000/items?userid=${userid}&locationid=${locid}&id=${itemId}`
      );

      if (res.status == 200) {
        this.getName = res.data;

        if (this.getName.length > 0) {
          this.name = this.getName[0].name;
        }
      }
    },
    goback() {
      this.redirectto({ val: 'ViewCaterogy' });
    },
    async canUserAccess() {
      let result = await axios.get(
        `http://localhost:3000/location?id=${this.locationId}&userid=${this.userId}`
      );

      if (result.status == 200 && result.data.length > 0) {
        this.locationData = result.data;
        this.name = this.locationData[0].name;
      } else {
        this.redirectto({ val: 'home' });
      }
    },
    async canUserAccessLocation() {
      let result = await axios.get(
        `http://localhost:3000/location?id=${this.locationId}&userid=${this.userId}`
      );

      if (result.status == 200 && result.data.length > 0) {
        this.locationData = result.data;
        this.locName = this.locationData[0].name;
        this.locAdress = this.locationData[0].adress;
      } else {
        this.redirectto({ val: 'home' });
      }
    },
    async deleteItem() {
      let result = await axios.delete(
        `http://localhost:3000/items/${this.itemId}`
      );
    //   let allitems = [];
    //   for (let i = 0; i < this.AllItemsId.length; i++) {
    //     let results = await axios.delete(
    //       `http://localhost:3000/items/${this.AllItemsId[i]}`
    //     );
    //     if (results.status == 200) {
    //       allitems.push(true);
    //     } else {
    //       allitems.push(false);
    //     }
    //   }
      if (result.status == 200) {
        this.redirectto({ val: 'ViewCaterogy' });
      } else {
        console.log('something went wrong');
      }
    },
  },
};
</script>
