<template>
  <div class="container">
    <NavBar></NavBar>
    <RouterLink
      :to="{ name: 'NewCaterogy', params: { locationID: locationId } }"
    >
      <button class="btn btn-success float-start">New caterogy</button>
    </RouterLink>
    
    &nbsp;&nbsp;&nbsp;
    <RouterLink :to="{ name: 'menu', params: { locationID: locationId } }">
      <button class="btn btn-info">back to menu</button>
    </RouterLink> 
    
    <router-link :to="{ name: 'DeleteAllcat' , params: { locationID: locationId } }">
          <button class="btn btn-danger float-end">
            Delete All
          </button></router-link>
    <br />
    <div class="">
      <table class="table caption-top" v-if="listOFcaterogies">
        <caption>
          list of loctions (
          {{
            numOfCaterogies
          }}
          )
        </caption>
        <thead class="table-dark">
          <tr>
            <th scope="col">Name</th>

            <th scope="col"
              colspan="2"
            >Actions</th>
          </tr>
        </thead >
        <tbody>
          <tr class="mb-5" v-for="(cat, i) in listOFcaterogies" :key="i">
            <th class="" scope="row">{{ cat.name }}</th>
            <th>

            <RouterLink
              :to="{
                name: 'UpdatePage',
                params: { locationID: cat.locationid, catId: cat.id },
              }"
            >
              <button class="btn btn-info">Update</button>
            </RouterLink> &nbsp;&nbsp;&nbsp; 
          </th>
          <th>

            <RouterLink
              :to="{
                name: 'DeleteCat',
                params: { locationID: cat.locationid, catId: cat.id },
              }"
            >
              <button class="btn btn-danger">Delete</button>
            </RouterLink>
            </th>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import NavBar from './NavBar.vue';
import { mapActions } from 'vuex';
import { mapMutations } from 'vuex';
import { mapGetters } from 'vuex';
import axios from 'axios';

export default {
  components: {
    NavBar,
  },
  data() {
    return {
      locationId: this.$route.params.locationID,
      userId: '',
      locName: '',
      locAdress: '',
    };
  },
  mounted() {
    let user = localStorage.getItem('user-item');
    if (!user) {
      this.redirectto({ val: 'signup' });
    } else {
      this.userId = JSON.parse(user).id;
      this.displayAll({ useridis: this.userId, locationIdIs: this.locationId });
      this.canUserSee({
        useridis: this.userId,
        locationIdIs: this.locationId,

        redirecttoPage: 'mainpage',
      });
    }
    this.getuser(this.userId, this.locationId);
  },
  computed: {
    ...mapGetters([
      'isLogedin',
      'userLogedID',
      'listOFcaterogies',
      'numOfCaterogies',
    ]),
  },
  methods: {
    ...mapActions(['redirectto']),
    ...mapMutations(['isLogedIn', 'displayAll', 'canUserSee']),
    async getuser(userId, locId) {
      let results = await axios.get(
        `http://localhost:3000/location?userid=${userId}&id=${locId}`
      );
      let details = [];
      if (results.status == 200) {
        details = results.data;
        this.locName = details[0].name;
        this.locAdress = details[0].adress;
      }
    },
  },
};
</script>
